.container {
  width: 1158px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}

.search-form {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  gap: 20px;
  background-color: darkcyan;
  margin-bottom: 30px;
}

.form-input {
  border-radius: 10px;
}

.form-buttom {
  border-radius: 10px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 10px;
}

.photo-card {
  width: 350px;
  overflow: hidden;
  border: 1px solid #8e8f99;
  border-radius: 10px;
  flex-basis: calc((100% - 2 * 20px) / 3);
}

.end-message {
  display: flex;
  justify-content: center;
  padding: 20px;
  background-color: crimson;
}

.hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
