.container {
  width: 1158px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.search-form {
  background-color: #008b8b;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  padding: 20px;
  display: flex;
}

.form-input, .form-buttom {
  border-radius: 10px;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 10px;
  display: flex;
}

.photo-card {
  width: 350px;
  border: 1px solid #8e8f99;
  border-radius: 10px;
  flex-basis: calc(33.3333% - 13.3333px);
  overflow: hidden;
}

.end-message {
  background-color: #dc143c;
  justify-content: center;
  padding: 20px;
  display: flex;
}

.hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

